:root{
    --background-color: lightblue;
    --board-background-color: thistle;
    --board-border-color: lightcoral;
    --panel-background-color: lightsteelblue;
    --panel-border-color: indianred;
}

body{
    background-color: var(--background-color);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.app-container{
    width: 800px;
    height: 800px;
    position: absolute;
    left: 50%;
    top: 10%;
    margin-left: -400px;
}

.panel{
    position: absolute;
    background-color: var(--panel-background-color);
    border: 8px solid var(--panel-border-color);
    -webkit-box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9);
    box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9);  
    z-index: 1;
}

.header{
    left: 50%;
    top: 90px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
}

.header-text{
    font-size: 30px;
    font-weight: bold;
    width: 200px;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.footer{
    left: 50%;
    top: 750px;
    height: 80px;
    width: 300px;
    margin-left: -120px;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.footer button{
    position: relative;
    top: 17%;
    left: 6%;
    width: 130px;
    height: 50px;
    background-color: lightskyblue;
    font-size: 20px;
    -webkit-box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9);
    box-shadow: 3px 5px 10px 2px rgba(0,0,0,0.9); 
    border: 5px solid chocolate;
    font-weight: bold;
}

.footer button:hover{
    background: chocolate;
}

.gameBoard{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr); 
    padding: 20px;
    width: 500px; 
    position: absolute;
    left: 50%;
    margin-left : -250px ;
    top: 20%;
    background-color: var(--board-background-color);
    border: 8px solid var(--board-border-color);
    border-radius: 8%;
    -webkit-box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
    box-shadow: 7px 5px 15px 3px rgba(0,0,0,0.9);
}

.gameCircle{
    width: 100px; 
    height:100px; 
    margin:10px;
    border-radius: 50%;
    border: 5px solid black;
}

.player_0{
    background-color: white;
}

.player_1{
    background-color: red;
}

.player_2{
    background-color: blue;
}